import React from 'react';
import KimTinhThachPage from './KimTinhThachPage';
import GameServerPage from './GameServerPage';
import AboutPage from './AboutPage';
class DashBoardPage extends React.Component {

  render() {

    return <div>
      <h2>WEB TOOLKIT CHO GAME TLBB</h2>

      <GameServerPage/>
      <KimTinhThachPage/>

      <br />
      <AboutPage/>

      <br />
      <div className="container">
        <strong>LingYun@Thanh Long - 2018</strong>
        <ul>
          <li><a href="https://youtube.com/click2teen" target="_blank">YouTube.com/click2teen</a></li>
          <li><a href="https://tlbb.readthedocs.io" target="_blank">tlbb.readthedocs.io</a></li>
          <li><a href="https://fb.com/chiase.tlbb" target="_blank">fb.com/chiase.tlbb</a></li>
        </ul>
      </div>
    </div>
  }
}

export default DashBoardPage;


